@use "../../../style//importStyles.scss" as mixin;
.albomCategory {
	position: relative;
	height: max-content;
	.BannerPhoto {
		max-height: 315px;
		overflow: hidden;
	}
	.albomContent {
		background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
		position: absolute;
		width: 100%;
		top: 0;
		bottom: 0;
		left: 0;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		padding: 19px 28px 28px 28px;
	}
}
.albomTools {
	display: flex;
	width: 100%;
	justify-content: space-between;
}
.albomLisen {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
.albomChoice {
	display: flex;
	align-items: center;
}
.albomFavorites {
	margin-right: 20px;
}
.albomNavItem {
	padding: 3px 0;
	.albomNavEl {
		@include mixin.STCategoryNav;
	}
}

.albomExecutor {
	@include mixin.STCategoryEx;
}
.albomTitle {
	@include mixin.STCategoryTitle;
}
